:root {
  --dark-textcolor: #ffffff;
  --dark-background: rgb(27, 25, 25);
  --light-textcolor: #000000;
  --light-background: #ffffff;
}

* {
  margin: 0;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  padding: 0px;
  width: 100%;
  height: 100%;
}

.app {
  height: 100%;
}

.app::after {
  content: "";
  clear: both;
  display: table;
}

.dark-mode {
  background-color: var(--dark-background);
  color: var(--dark-textcolor);
  height: -webkit-fill-available;
}
.dark-mode .footer {
  background-color: var(--dark-background);
}

.light-mode {
  background-color: var(--light-background);
  height: -webkit-fill-available;
  color: var(--light-textcolor);
}
.light-mode .footer {
  background-color: var(--light-background);
}

.header {
  letter-spacing: 9px;
  font-weight: 900;
  font-family: Arial, Helvetica, sans-serif;
}

.emoji {
  transition: all 0.3s ease-in-out;
}

.emoji:hover {
  transform: scale(1.2);
}

.content {
  height: 100%;
  padding: 0;
  width: 100%;
  padding: 200px 0;
  overflow: hidden;
  box-sizing: border-box;
}

.img-header {
  text-align: center;
}
.img-header button {
  padding: 5px 15px;
  margin-right: 15px;
}

.numbel {
  margin-bottom: 20px;
}

.para3 {
  margin-bottom: 20px;
}

.white-btn {
  width: 150px;
  height: 50px;
  border: none;
  outline: none;
  color: var(--dark-textcolor);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.white-btn:before {
  content: "";
  background: linear-gradient(45deg, #645e5e, #887e76);
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  -webkit-animation: glowing 20s linear infinite;
          animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.white-btn:active {
  color: #000;
}

.white-btn:active:after {
  background: transparent;
}

.white-btn:hover:before {
  opacity: 1;
}

.white-btn:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(183, 178, 178);
  left: 0;
  top: 0;
  border-radius: 10px;
}

@-webkit-keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}
