.arrow {
  padding-left: 10px;
  padding-top: 4px;
  box-sizing: border-box;
  color: white;
}

.project-list {
  display: flex;
  padding: 15px;
  box-sizing: border-box;
  width: 100%;
}

.project-img {
  padding-left: 10px;
}

.my-projects {
  width: 15%;
  border: none;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  padding: 10px 5px;
  margin-right: 30px;
  background-color: rgb(41, 38, 38);
  box-shadow: 2px 10px 9px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  transition: all 0.1 ease-in-out;
}

.my-projects:hover {
  transform: scale(1.1);
}

.project-info {
  padding-left: 5px;
  box-sizing: border-box;
}
.project-info h3 {
  padding-left: 10px;
}

.lightmode-btn {
  border-radius: 50%;
  position: absolute;
  top: 4%;
  right: 4%;
  border: none;
  cursor: pointer;
  -webkit-animation: glowing 20s linear infinite;
          animation: glowing 20s linear infinite;
  background: transparent;
  height: 30px;
  width: 30px;
  z-index: 0;
  outline: none;
  background-color: var(--light-background);
  box-shadow: 2px 10px 5px rgb(0, 0, 1);
  transition: all 0.3s ease-in-out;
}

.lightmode-btn:hover {
  transform: scale(1.2);
}

.footer {
  text-align: center;
  box-shadow: -2px -10px 5px rgb(0, 0, 1);
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  position: absolute;
  bottom: 0;
}

.footer-icon {
  width: 40px;
  height: 20px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.footer-icon:hover {
  transform: scale(1.4);
}

.footer::after {
  content: "";
  clear: both;
  display: table;
}/*# sourceMappingURL=style.css.map */